<template>
  <PageContent v-loading="loading">
    <div slot="form" class="page-form">
      <form-item label="查询内容">
        <el-input placeholder="请输入查询内容" v-model="searchParams.Payload" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <div class="btn-box">
        <el-button size="small" type="primary" @click="search">查询</el-button>
      </div>
    </div>
    <div class="page-main">
      <ListLayout>
        <el-table slot="list" :data="tableData" width="100%" height="100%" :border="false">
          <el-table-column label="探针主机" prop="HostName"></el-table-column>
          <el-table-column label="探针IP" prop="AgentIp">
            <template v-slot="{row}">
              <div v-for="(item,index) in (row.AgentIp || '').split(',')" :key="index">{{item}}</div>
            </template>
          </el-table-column>
          <el-table-column label="系统类型" prop="SystemType"></el-table-column>
          <el-table-column label="注册时间" prop="CreateTime">
            <template v-slot="{row}"> 
              <div>{{$moment(row.CreateTime * 1000).format('YYYY/MM/DD hh:mm:ss')}}</div>
            </template>
          </el-table-column>
          <el-table-column label="心跳时间" prop="HeartbeatTime">
            <template v-slot="{row}">
              <div>{{$moment(row.HeartbeatTime * 1000).format('YYYY/MM/DD hh:mm:ss')}}</div>
            </template>
          </el-table-column>
          <el-table-column label="探针状态">
            <template v-slot="{row}">
              <span v-if="agentStatusMap[row.Status]" :class="agentStatusMap[row.Status].color">{{agentStatusMap[row.Status].text}}</span>
            </template>
          </el-table-column>
          <el-table-column label="沦陷状态">
            <template v-slot="{row}">
              <span v-if="lostStatusMap[row.LostStatus]" :class="lostStatusMap[row.LostStatus].color">{{lostStatusMap[row.LostStatus].text}}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="100px">              
            <template v-slot="{row}">
              <el-tooltip class="item" effect="dark" content="代理列表" placement="bottom">
                <el-button type="primary" size="mini" icon="el-icon-tickets" circle @click="showTransformDialog(row)"></el-button>
              </el-tooltip>
              <el-tooltip content="诱饵列表" placement="bottom">
                <el-button type="primary" size="mini" icon="esign-icon-icon-test" circle @click.native="showBaitDialog(row)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
          <div class="table-empty" slot="empty" style="width: 100%;">
            <img src="@/assets/images/empty.png" alt="" width="305" height="159" />
          </div>
        </el-table>
        <Pagination slot="pagination" @size-change="sizeChange" @current-change="getTableData" :total="pagination.totalCount" :pageSize="pagination.size" :current-page.sync="pagination.page" />
      </ListLayout>
    </div>
    <!-- 诱饵列表 -->
    <BaitListDialog v-if="baitListDialogCtrl.isShow" :ServerID="baitListDialogCtrl.AgentToken" @close="baitListDialogCtrl.isShow=false" />
    <TransparentListDialog v-if="transparentDialogCtrl.isShow" :dialogData="transparentDialogCtrl.data" @close="transparentDialogCtrl.isShow=false" @save="search" />

  </PageContent>
</template>

<script>
import BaitListDialog from './BaitListDialog.vue';
import TransparentListDialog from './TransparentListDialog.vue';

import pageList from '@/mixins/pageList';

export default {
  components: { 
    BaitListDialog,
    TransparentListDialog
  },

  mixins: [pageList],

  data () {
    return {
      searchUrl: this.$apis.probeListSearch,
      // 诱饵列表弹窗
      baitListDialogCtrl: {
        isShow: false,
        AgentToken: ''
      },
      transparentDialogCtrl: {
        isShow: false,
        data: ''
      }
    }
  },

  created () {
    this.search();
  },

  methods: {
    /**
     * 编辑镜像
     */
    editRow (row) {
      this.editDialogCtrl = {
        isShow: true,
        data: { ...row }
      }
    },

        /**
     * 显示透明转发列表
     */
     showTransformDialog(row) {
      this.transparentDialogCtrl = {
        isShow: true,
        data: row
      }
    },

    /**
     * 显示诱饵列表弹窗
     */
    showBaitDialog(row) {
      this.baitListDialogCtrl = {
        isShow: true,
        AgentToken: row.AgentToken
      };
    }
  }
}
</script>